import { lazy } from 'react';

const MainPage = lazy(() => import('../pages/MainPage/MainPage'));
const OrderPage = lazy(() => import('../pages/OrderPage/OrderPage'));
const MyOrderPage = lazy(() => import('../pages/MyOrderPage/MyOrderPage'));

export const routePaths = {
  mainPage: '/',
  orderPage: '/order',
  myOrderPage: '/myOrder',
  parking: '/parking',
  insurance: '/insurance',
  benzine: '/benzine',
  service: '/service',
};

export const publicRoutes = [
  {
    path: routePaths.mainPage,
    exact: true,
    component: MainPage,
    label: '',
  },
  { path: routePaths.orderPage, exact: true, component: OrderPage, label: ' ' },
  {
    path: `${routePaths.myOrderPage}/:id`,
    exact: true,
    component: MyOrderPage,
    label: ' ',
  },
  {
    path: routePaths.parking,
    exact: true,
    component: () => <div></div>,
    label: '',
  },
  {
    path: routePaths.insurance,
    exact: true,
    component: () => <div></div>,
    label: '',
  },
  {
    path: routePaths.benzine,
    exact: true,
    component: () => <div></div>,
    label: '',
  },
  {
    path: routePaths.service,
    exact: true,
    component: () => <div></div>,
    label: '',
  },
];
